<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">指引单</div>

      <div class="flex">
        <el-form size="small"
          inline
          class="default-form-style">
          <el-form-item label="体检流水号："
            style="margin-bottom:0">
            <el-input placeholder="请输入"
              v-model="examUserId"
              clearable></el-input>
          </el-form-item>
          <el-button icon="el-icon-search"
            style="margin: 0 30px"
            size="small"
            @click="getGuideSheet(examUserId)"
            round>查询</el-button>
        </el-form>
        <!--  @click="printPage" -->
        <div class="head-btn"
          @click="printPage">
          <img src="@/assets/img/d4.png"
            alt="打印">&nbsp;<span>打印</span>
        </div>
      </div>
    </div>
    <!-- <el-button @click="download">下载文件</el-button> -->
    <!-- <el-col v-if="formShow">
      <el-form label-width="auto">
        <el-form-item label="姓名：">
          <el-input></el-input>
        </el-form-item>
      </el-form>
    </el-col> -->
    <!-- <el-card v-if="formShow" style="width:600px;margin:50px auto;">
      <el-tabs stretch="stretch">
        <el-tab-pane label="基本信息">
          <el-form label-width="auto">
            <el-form-item>
              <el-form-item label="任务名称">
                <el-input></el-input>
              </el-form-item>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="运行参数">运行参数</el-tab-pane>
      </el-tabs>
    </el-card> -->

    <div class="container flex-item"
      ref='print-html-box'>
      <guide-sheet-html class="guide-sheet"
        :user='user'
        :guideSheetData='guideSheetData'></guide-sheet-html>
    </div>
  </div>
</template>

<script>
import printMethod from '@/lib/print-method'
import GuideSheetHtml from '@/components/GuideSheetHtml'
export default {
  name: 'GuideSheet',
  components: { GuideSheetHtml },
  data() {
    return {
      formShow: true,
      user: {},
      guideSheetData: [],
      examUserId: ''
    }
  },

  created() {
    this.initData()
  },
  methods: {
    initData() {
      this.$post('/user/examUser/showReportExamList', {
        pageNum: 1,
        pageSize: 1
      })
        .then(({ data }) => {
          this.getGuideSheet(data.list[0].sysGuid)
        })
        .catch(err => this.$message.error(err))
    },

    printPage() {
      printMethod(this.$refs['print-html-box'], '体检指引单')
      let loading = this.$loading()
      setTimeout(() => loading.close(), 800)
    },

    getGuideSheet(examUserId) {
      if (examUserId.trim() === '') return
      this.$post('/user/examUser/showReportGuide', {
        examUserId
      }).then(({ data }) => {
        let { projectList, user } = data
        this.user = user
        this.guideSheetData = projectList
      })
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.container {
  padding: 120px 20px;
  overflow-y: auto;
}
